import React from "react";
import logo from "../imagenes/logoProcu.png";
import { Link } from "react-router-dom";
const styleDiv = {
  marginTop: "30px",
};

const p = {}
var prueba = () => {
  if(document.getElementById("inputBuscar").value);
  {
    var valor = document.getElementById("inputBuscar").value;
    p.entrada = valor;
  }
}

function Home() {
  return (
    <div id="divHome">
      
      <div id="divTitulo">
        <h1 id="h1Titulo">Buscador SIM</h1>
      </div>
      <div id="divTitulo2">
        <h3 id="h3Titulo">Procuradur√≠a General de la Naci√≥n</h3>
      </div>
      <div id="divSearch">
        <form id="formBuscar">
          <input id="inputBuscar" type="search" />
          <i className="fa fa-search"></i>
        </form>
      </div>
      <div className="row" style={styleDiv}>
        <div className="col">
          <Link
            to={{
              pathname: "/Buscar",
              state: {
                busqueda: "sencilla",
                param: {p}
              },
            }}
          >
            <button className="botonHome btn" id="botonB"onClick={prueba}>Buscar</button>
          </Link>
        </div>
        <div className="col">
          <Link
            to={{
              pathname: "/Buscar",
              state: {
                busqueda: "avanzada",
                param: {p}
                
              },
            }}
          >
            <button className="botonHome btn" id="botonBA" onClick={prueba}>Busqueda Avanzada</button>
          </Link>
        </div>
      </div>

    </div>
  );
}

export default Home;
